.header{
	background: deepskyblue; /* For browsers that do not support gradients */
    background: -webkit-gradient(linear, left bottom, left top, from(deepskyblue), to(#ff6699));
    background: -webkit-linear-gradient(bottom, deepskyblue, #ff6699);
    background: -o-linear-gradient(bottom, deepskyblue, #ff6699);
    background: linear-gradient(to top, deepskyblue, #ff6699);
	position: relative;
	width: 100%;

}

h1{
	text-align: center;
	font-size: 80px;
	font-weight: bold;
	padding: 90px 0 20px 0; 
	margin: 0px;
	font-family: 'Old Standard TT', serif;
	background-color: transparent;
	letter-spacing: 18px;
	text-shadow: 3px 2px #777;
}

h2{
	text-align: center;
	font-size: 30px;
	padding-bottom: 40px;
	margin: 0px;
	font-family: 'Audiowide', cursive;
	text-shadow: 3px 2px #777;
}


